import './setPublicPath'
import Vue from 'vue'
import wrap from '@vue/web-component-wrapper'

// runtime shared by every component chunk
import 'css-loader/dist/runtime/api.js'
import 'vue-style-loader/lib/addStylesShadow'
import 'vue-loader/lib/runtime/componentNormalizer'

import HelloWorld from '~root/src/components/HelloWorld.vue?shadow'
window.customElements.define('wc-hello-world', wrap(Vue, HelloWorld))

import MyComponent from '~root/src/components/MyComponent.vue?shadow'
window.customElements.define('wc-my-component', wrap(Vue, MyComponent))

import ScInput from '~root/src/components/ScInput.vue?shadow'
window.customElements.define('wc-sc-input', wrap(Vue, ScInput))

import ScSelect from '~root/src/components/ScSelect.vue?shadow'
window.customElements.define('wc-sc-select', wrap(Vue, ScSelect))